/* ==========================================================================
3. Hero Area
========================================================================== */
#hero-area{
	background: url(../img/hero-area.jpg) no-repeat;
	background-size: cover;
	color: #fff;
	overflow: hidden;
	position: relative;
	.overlay{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0px;			
		left: 0px;
	    background: rgba(0, 0, 0, 0.7);
	}
	.contents{
		padding: 160px 0 210px;
		.head-title{	
			color: #fff;
		    font-size: 60px;
		    font-weight: 700;
		    text-transform: uppercase;
		    margin-bottom: 15px;
		}
		span.year{
	    	color: $preset;
	    }
		p{
			font-size: 16px;
			color: #fff;
			font-weight: 400;
			line-height: 26px;
		}
	}
}
.search-bar{
	margin-top: 40px;
	.search-form{
		width: 100%;
		float: left;
	}
	.search-inner{
		background: #fff;
		overflow: hidden;
		border-radius: 50px;
	}
	.search-inner{
	    margin: 0;
	    border: 0;
	    padding: 10px;
	    width: 100%;
	    float: left;
	    position: relative;
	    background: #808080;
	}
	.form-group:first-child {
	    border: 0;
	}
	.form-group {
        margin: 5px;
	    float: left;
	    width: 27.33%;
	    padding: 0 0 0 52px;
	    background: #fff;
	    border-radius: 50px;
	    .form-control{
	    	position: relative;
	    	z-index: 1;
			border: 0;
		    height: 50px;
		    border-radius: 4px;
		    font-size: 15px;
		    line-height: 30px;
		    padding: 15px 15px 15px 0;
		}
		.form-control, .select select {
		    border: 0;
		    height: 50px;
		    border-radius: 50px;
		    font-size: 15px;
		    line-height: 30px;
		    padding: 10px 15px 15px 0;
		    background: #ffffff;
		}
		.form-control, .select select:focus{
			outline: none;
			box-shadow: none;
		}
	}
	.select {
		color:#666;
		float:left;
		width: 100%;
		position: relative;
		text-transform:uppercase;
	}
	.select:after {
		top: 7px;
		right: 15px;
		z-index: 2;
		color: #666;
		display: block;
		content: '\e93a';
		position: absolute;
		text-align: center;
		font-size: inherit;
		line-height: 40px;
		font-family: 'LineIcons';
	}
	.select select {
		z-index: 1;
		width: 100%;
		position: relative;
		appearance: none;
		-moz-appearance: none;
		-webkit-appearance: none;
	}
	.select select option { color:#666; }
	.inputwithicon {
	    position: relative;
	}
	.inputwithicon i {
		top: 0;
	    left: 0;
	    z-index: 2;
	    color: $preset;
	    position: absolute;
	    left: 20px;
	    font-size: 20px;
	    line-height: 50px;
	}
	.btn-common {
	    top: 15px;
	    right: 15px;
	    padding: 0;
	    z-index: 2;
	    width: 157px;
	    border-radius: 50px;
	    border: none;
	    cursor: pointer;
	    line-height: 50px;
	    text-align: center;
	    position: absolute;
	    font-weight: 700;
	    letter-spacing: 0.5px;
	    text-transform: uppercase;
	    transition: all 0.4s ease-in-out;
	    -moz-transition: all 0.4s ease-in-out;
	    -webkit-transition: all 0.4s ease-in-out;
	    -o-transition: all 0.4s ease-in-out;
	    i{
	    	font-size: 20px;
	    	vertical-align: middle;
	    }
	}
}
.search-button{
	background: #484848;
	padding-bottom: 40px;
}


